import styled from '@emotion/styled';

export const MainField = styled.div(props => ({
	backgroundPosition: 'center',
	backgroundSize: 'cover',
	width: '100%',
	height: '100vh',
	display: 'flex',
	alignItems: 'center',
	justifyContent: 'center',
	flexDirection: 'column',
	padding: '30px 0',

	h3: {
		fontSize: '9rem',
		color: '#F9B233',
	},

	h4: {
		color: '#3c3c50',
		fontSize: '3rem',

		'@media (max-width: 600px)': {
			fontSize: '2rem',
		},
	},

	p: {
		fontSize: '2rem',
		width: '700px',
		textAlign: 'center',
		lineHeight: '50px',
		color: '#3c3c50',
		marginBottom: '20px',

		'@media (max-width: 700px)': {
			width: '400px',
			fontSize: '1.5rem',
			lineHeight: '40px',
		},
		'@media (max-width: 450px)': {
			width: '90%',
			fontSize: '1.2rem',
		},
	},

	div: {
		backgroundImage: `url(${props.image})`,
		width: '100%',
		backgroundPosition: 'center',
		backgroundSize: 'cover',
		minHeight: '200px',

		'@media (max-width: 700px)': {
			minHeight: '130px',
		},
		'@media (max-width: 450px)': {
			minHeight: '80px',
		},
	},

	button: {
		color: 'white',
		backgroundColor: '#F9B233',
		boxShadow: 'none',
		borderRadius: '50px',
		padding: '7px 20px',
		textTransform: 'capitalize',
		marginBottom: '20px',

		'&:hover': {
			boxShadow: 'none',
			border: '1px solid #F9B233',
			color: '#F9B233',
			backgroundColor: '#f9b2330f',
		},
	},
}));
